export class Ball {
  constructor({ x, y, dx, dy, maxEnergy, radius, canvas, fillStyle }) {
    this.x = x;
    this.y = y;
    this.dx = dx;
    this.dy = dy;
    this.maxEnergy = maxEnergy;
    this.radius = radius;
    this.canvas = canvas;
    this.fillStyle = fillStyle;
  }

  energy = this.maxEnergy;
  outOfBounds = false;

  updateMovement() {
    const { x, y, dx, dy, canvas, energy, outOfBounds, radius } = this;
    if (energy > 0) {
      energy -= 0.01;
    }
    x += dx * energy;
    y += dy * energy;
    const outCanvasWidth = x <= 0 + radius || x >= canvas.width - radius;
    const outCanvasHeight = y <= 0 + radius || y >= canvas.height - radius;
    // flip direction if ball reaches edge of canvas
    if (outCanvasWidth && !outOfBounds) {
      dx = -dx;
    }
    if (outCanvasHeight && !outOfBounds) {
      dy = -dy;
    }

    const isOutOfBounds = (outCanvasWidth || outCanvasHeight) && !outOfBounds;
    if (isOutOfBounds) {
      outOfBounds = true;
    } else {
      outOfBounds = false;
    }
  }

  draw() {
    const { canvas, radius, fillStyle, updateMovement } = this;
    const ctx = canvas.getContext('2d');
    ctx.beginPath();
    ctx.arc(x, y, radius, 0, 2 * Math.PI);
    ctx.globalAlpha = 1;
    ctx.fillStyle = fillStyle;
    ctx.fill(); // fill the circle with the current fill color
    ctx.closePath();
    updateMovement();
  }

  // TODO - refactor clickBall fn over to here.
}
